@font-face {
  font-family: 'coditor';
  src:  url('fonts/coditor.eot?cig8i');
  src:  url('fonts/coditor.eot?cig8i#iefix') format('embedded-opentype'),
    url('fonts/coditor.ttf?cig8i') format('truetype'),
    url('fonts/coditor.woff?cig8i') format('woff'),
    url('fonts/coditor.svg?cig8i#coditor') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'coditor' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

